import http from "../http";


export { getPhoneListService, uploadFileService }

//获取用户小区列表
const getPhoneListService = (params) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-property-house/house/commonTelephoneMgr/listNewCommonTelephone', params, "baseUrl", false).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}
//图片上传
const uploadFileService = (params) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-sys-media/media/upload/uploadBase64File', params, true,"homeUrl",true,false).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}